.DailyCard {
  padding: 10px;
  flex: 1 0 11%;
  background-color: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  border-radius: 10px;
  margin: 30px 5px;
}
.DailyCard_temp span {
  padding: 10px;
  font-size: 1.3rem;
}

.DailyCard__weekday {
  font-size: 1.2rem;
  font-weight: bold;
  text-transform: capitalize;
}

@media (max-width: 1100px) {
  .DailyCard {
    flex: 1 0 22%;
  }
}
